<template>
  <header>
    <div class="left">
      <img src="../../assets/backico.png" v-if="icoS == 'backI' && icoSS">
      <img src="../../assets/closeico.png" v-if="icoS == 'closeI' && icoSS">
    </div>
    <p class="h-1">{{ htitle }}</p>
    <div class="right">
      <img src="../../assets/addico.png" v-if="icob == 'plusI' && icobS">
      <p v-if="icob == 'wordI' && icobS">{{ changew }}</p>
    </div>
  </header>
</template>

<script>
  export default {
    name: 'Header',
    data() {
      return {}
    },
    props: {
      htitle: String,
      icoS: String,
      icoSS: Boolean,
      icob: String,
      icobS: Boolean,
      changew: String
    }
  }
</script>

<style lang="less" scoped>
  header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    height: 1.333333rem;
    padding: 0 .333333rem;
    width: 100%;
    margin-bottom: .266667rem;
    .left {
      img {
        width: .213333rem;
        height: .4rem;
      }
    }
    .h-1 {
      font-size: .48rem;
      font-weight: bolder;
    }
    .right {
      img {
        width: .4rem;
        height: .4rem;
      }
      p {
        font-size: .373333rem;
      }
    }
  }
</style>



